
import { defineComponent, ref } from 'vue'
export default defineComponent({
  name: 'Example2',
  setup () {
    const list = ref(Array.from(new Array(10).keys()))
    const text = ref('')
    const eventReply = (d: string, i: number) => {
      text.value += `模块:${i}===>触发:${d}事件\n`
    }
    return {
      list,
      text,
      eventReply
    }
  }
})
