
import { defineComponent, nextTick, ref } from 'vue'
import { useTv } from '@/plugins/focusable'
export default defineComponent({
  name: 'Example4',
  setup () {
    const $tv = useTv()
    const popUpRef = ref()
    const sRefs = ref()
    const openBut = ref()
    const list = ref(Array.from(new Array(245).keys()))
    const isShow = ref(false)
    const open = () => {
      isShow.value = true
      nextTick(() => {
        $tv.setLimitingEl(popUpRef.value)
        $tv.onFocus(sRefs.value[1])
      })
    }

    const close = () => {
      $tv.resetLimitingEl()
      $tv.onFocus(openBut.value)
      isShow.value = false
    }

    return {
      popUpRef,
      openBut,
      sRefs,
      isShow,
      list,
      open,
      close
    }
  }
})
